@use "../../utils/colors";

a {
  all: unset;
}

.header {
  min-height: 80px;
  max-height: 150px;
  box-shadow: 0 1px 3px rgb(0 0 0 / 10%), 0 2px 2px rgb(0 0 0 / 6%), 0 0 2px rgb(0 0 0 / 7%);
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  position: sticky;
  padding: 0 2.7%;
  top: 0;
  z-index: 10;
  background: white;

  .header--wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .header--right {
    display: flex;
    align-items: center;
    max-width: 350px;
    width: 100%;
    justify-content: space-between;
  }

  .header--left {
    display: flex;
    align-items: center;
    max-width: 380px;
    width: 100%;
    justify-content: space-between;
  }

  .header-icon {
    margin-right: 40px;
    cursor: pointer;
    margin-top: 15px;
    width: 50px;
    height: 50px;
  }

  .header--navigation {
    display: flex;
    box-sizing: border-box;
    width: 100%;
    max-width: 300px;

    .navLink {
      display: flex;
      align-items: center;
    }

    .navigation--list {
      display: inherit;
      justify-content: space-between;

      width: 100%;
      align-items: center;

      list-style: none;

      .navigation--list--item {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 14px;
        cursor: pointer;
        height: 80px;
        display: inherit;
        align-items: center;
        box-sizing: border-box;
      }

      a {
        height: 80px;
        box-sizing: border-box;
        border-bottom: 5px solid white;
      }

      .active--tab {
        color: colors.$dark_green;
        border-bottom: 5px solid colors.$dark_green;
      }

      .navigation--list--item:hover {
        color: colors.$dark_green;
      }
    }
  }

  .header--location {
    display: flex;
    justify-content: space-evenly;

    width: 100%;
    max-width: 130px;

    cursor: pointer;

    .location--pin {
      width: 23px;
      height: 23px;
    }

    .location--text {
      font-weight: 500;
      font-size: 14px;
    }
  }

  .header--location:hover {
    filter: invert(17%) sepia(79%) saturate(6188%) hue-rotate(161deg) brightness(81%) contrast(98%);
  }

  .header--nav {
    display: flex;
  }
}
.no-margin {
  padding: 0 1.1%;
}

@media (max-width: 1919px) and (min-width: 1441px) {
  .header {
    padding: 0 7%;
  }

  .header--left {
    margin-right: 80px;
  }
}

@media (max-width: 800px) {
  .header--location {
    display: none !important;
  }

  .header--left {
    justify-content: flex-end !important;
  }
}

@media (max-width: 767px) {
  .header--left {
    width: 190px !important;
  }

  .header--right {
    max-width: 240px !important;
  }

  .header-icon {
    margin-right: 25px !important;
  }
}

@media (max-width: 600px) {
  .header--left {
    display: none !important;
  }
}
