@use '../../utils/colors';

.login-footer{
    width: 100%;
    max-height: 210px;
    display: flex;
    justify-content: center;
    margin-top: 40px;
    font-weight: 400;
    padding-bottom: 40px;

    .footer--wrapper{
        max-width: 500px;
        display: flex;
        flex-direction: column;
        width: 100%;

        .footer--row{
            display: flex;
            width: 100%;
            justify-content: space-between;
            margin-bottom: 20px;

            .footer--button{
                background: none;
                border-radius: 50px;
                border-style: solid;
                border-width: 1px;
                display: inline-block;
                font-weight: 600;
                line-height: 1.2;
                padding: 7px 16px;
                text-align: center;
                text-decoration: none;
                transition: all .2s ease;
                color: colors.$dim_green;
                width: 130px;
                height: 40px;
                box-sizing: border-box;
                cursor: pointer;
            }

            .footer--button:hover{
                background-color: rgba(0, 117, 74, 0.1)
            }

            .footer--list{
                list-style: none;
                height: 120px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
            }

            .footer--list>li{
                cursor: pointer;
            }
        }
    }
}



